@import "../../Assets/scss/colors.scss";

.house__type {
    color: $cardBlue;
    font-weight: 500 !important;
    font-size: 12px !important;
    line-height: 10px !important;
    padding: 3px 7px;
    border: 1px solid $cardBlue;
    border-radius: 15px;
    cursor: pointer;
    height: 20px;
}
.house__prices {
    font-weight: 500;
    font-size: 12px;
    line-height: 10px;
    color: #6d7580;
}
.house__price {
    font-weight: 600;
    font-size: 16px;
    line-height: 15px;
    color: $cardBlue;
}
.house__address {
    font-weight: 400;
    padding-left: 5px;
}
.card__title {
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    padding: 8px 0 10px !important;
    display: inline-block;
    margin: 0;
    color: #000;
}
.failText {
    padding: 10px 20px;
    background-color: #ffacac;
    color: #111;
    font-weight: 500;
    border-radius: 12px;
    width: 100%;
    text-align: center;
}
.card {
    border-radius: 12px !important;
    box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.4) !important;
    margin-bottom: 15px;
    position: relative;
    min-width: 200px;
    width: 100%;
    padding-bottom: 3px;
    &__header,
    &__footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__img {
        // object-fit: cover;
        // width: 100%;
        // height: 100%;
    }
    &__main {
        padding: 0 16px !important;
    }
    &__footer {
        padding: 0 16px 10px !important;
        p {
            display: flex;
            align-items: center;
            font-weight: 400 !important;
            font-size: 18px;
            line-height: 22px;
            color: #6d7580;
        }
    }
    &__btn img {
        width: 16px;
        height: 16px;
    }
    &__call,
    &__msg {
        background: radial-gradient(
            100% 100% at 50.44% 100%,
            #367bff 0%,
            #6197ff 100%
        );
    }
    &__call {
        padding: 8px 16px !important;
        border-radius: 5px !important;
    }
    &__msg {
        margin: 0 !important;
    }
    .love-btn {
        position: relative;
        cursor: pointer;
        .content {
            width: 33px;
            height: 33px;
            display: flex;
            border-radius: 50% !important;
            border: 2px solid #eae2e1;
            border-radius: 5px;
            cursor: pointer;
            pointer-events: none;
        }
        .content.active {
            border-color: #f9b9c4;
            background: #fbd0d8;
        }
        .heart {
            position: absolute;
            background: url("../../Assets/Img/love-animate.png") no-repeat;
            background-position: left;
            background-size: 2900%;
            height: 60px;
            width: 60px;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            pointer-events: none;
        }
        .heart.active {
            animation: animate 0.8s steps(28) 1;
            background-position: right;
        }
        @keyframes animate {
            0% {
                background-position: left;
            }
            100% {
                background-position: right;
            }
        }
    }
}
.scard {
    margin-right: 30px;
    min-width: 350px;
    width: 390px;
}
.fullCard {
    width: 100%;
    padding: 20px 20px 20px 30px;
    display: flex;
    box-shadow: none !important;
    background-color: #f6f6f6 !important;
    border-radius: 15px !important;
    margin-bottom: 35px;
    &__img {
        max-height: 200px;
        height: 200px;
        max-width: 350px;
        min-width: 300px;
        border-radius: 10px;
    }
    .card__header {
        display: flex;
        justify-content: space-between;
        width: 100%;
        padding-top: 10px;
        padding-bottom: 10px;
    }
    .card__header__items {
        display: flex;
        align-items: center;
    }
    .card__content {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .card__title {
        margin-right: 10px;
    }
    .house__prices {
        min-width: auto !important;
        margin-left: 10px;
    }
    .card__desc {
        height: auto;
        font-size: 16px;
        font-weight: 400;
        line-height: 22px;
        display: inline-block;
        margin-bottom: 15px;
        width: 60%;
    }
    .card__btn {
        margin-left: 10px !important;
    }
    &__foot {
        display: flex;
        align-items: center;
    }
}
